import Vue from 'vue';
import Component from 'vue-class-component';
import { Action, State } from 'vuex-class';

import Switch from 'muse-ui/switch';

import Api from '~/api';
import { ToastMsg } from '~/entity/toast';

@Component({
    components: {
        'mu-switch': Switch
    }
})
export default class Comp extends Vue {

    @Action('showToast')
    showToast;

    @Action('getBindInfo')
    getBindInfo;

    // 红娘定价
    @State(state => state.user.price)
    price: number;

    @State(state => state.user.matchDisplay)
    display: number;

    get isShow() {
        console.log(this.display);
        return this.display === 1;
    }

    get priceText() {
        return (this.price / 100).toFixed(2) + '元人/次';
    }

    async changeShow(value) {
        console.log(value);
        // 提交修改
        try {
            let isShow = 0;
            value ? isShow = 1 : isShow = 0;
            const res = await Api.updatePriceDisplay(isShow);
            // 更新绑定信息
            this.getBindInfo();
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        }
    }

    toSetPricePage() {
        // 
        this['$router'].push({
            name: 'setPrice'
        });
    }
}



